.footer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
}

.form{
    display: flex;
    align-items: center;
}

.circle{
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #F44545;
    margin-right: 10px;
}

.text{
    font-family: 'Roboto',sans-serif;
    font-size: 12px;
    font-weight: 400;
    color: #5A5A5A;
}

.button{
    padding: 8px 20px !important;
    background-color: #A1DCE0 !important;
    outline: none;
    border-radius: 8px !important;
    font-size: 14px;
    font-weight: 500 !important;
    color: #fff !important;
    transition: background-color .2s ease-in;
}

.button:hover{
    background-color: #00A0AB !important;
}

@media (max-width: 660px) {
    .footer{
        flex-direction: column;
    }
}