.card{
    position: relative;
    width: 112px;
    height: 130px;
    background-color: #E9E9E9;
    border-radius: 1px;
    cursor: pointer;
    transition: .2s ease;
    overflow: hidden;
}

.card:hover{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    transform: scale(1.1);
}

.tuchboard{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 4px;
    position: absolute;
    z-index: 1;
}

.tuchboard__btn{
    border: none;
    background-color: initial;
    outline: none;
}


.first{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 112px;
    height: 18px;
    position: absolute;
    left: 0;
    bottom: 0;
    background: rgba(44, 44, 44, 0.37);
    border-radius: 1px;
    z-index: 1000;
}

.svg{
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
}
.svg__delete{
    left: initial;
    right: -80px;
}
.text{
    margin: 0;
    font-family: 'Roboto',sans-serif;
    font-size: 12px;
    font-weight: 400;
    color: #FFF;
}

.svg:hover .hover{
    fill: #00A0AB;
}

.next{
    display: none;
}

.photo{
    width: 112px;
    height: 130px;
    object-fit: fill;
}
