.checkbox{
    display: flex;
    align-items: center;
    margin-left: 13px;
}

.input{
    width: 0;
    height: 0;
    visibility: hidden;
}

.label{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    width: 16px;
    height: 16px;
    box-sizing: border-box;
    border: 1px solid #2C2C2C;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;
    transition: background .2s ease-in;
}

.label:hover{
    background-color: #00A0AB;
}

.input:checked + .label{
    background-color: #00A0AB;
    transition: opacity .2s ease-in;
}

.input:checked + .label:hover{
    opacity: 0.2;
}

.text{
    pointer-events: none;
    margin-left: 3px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #5A5A5A;
}

@media (max-width: 620px) {
    .checkbox{
        margin: 8px 0;
    }
}