.form{
    display: flex;
    width: 490px;
    position: relative;
}
/*input UI*/
.input{
    display: flex;
    width: 100%;
    height: 28px;
    font-size: 14px;
    font-weight: 400;
    border-radius: 8px;
    border: 1px solid #8F8F8F;
    color: #5A5A5A;
    outline: none;
    transition: border .2s ease-in;
    padding: 0 16px;
    position: relative;
}

.input::placeholder{
    color: #C7C7C7;
}

.input:valid{
    color: #5A5A5A;
}

.input:focus{
    border: 1px solid #00A0AB;
}

.conditions{
    position: absolute;
    bottom: -20px;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    line-height: 1.75em;
    color: #C7C7C7;
}

.circle{
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    top: 50%;
    left: -13px;
    transform: translateY(-50%);
    background-color: #F44545;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

@media (max-width: 610px) {
    .input{
        width: 70%;
    }
}

@media (max-width: 540px) {
    .input{
        width: 50%;
    }

    .conditions{
        font-size: 9px;
    }
}

@media (max-width: 400px) {
    .input{
        width: 36%;
    }
    .form{
        flex-direction: column;
    }
    .conditions{
        position: static;
        width: 200px;
    }
}