.progress__item{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
}

.progress__text{
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    font-weight: 500;
    text-transform: capitalize;
    color: #C7C7C7;
    margin-right: 6px;
    line-height: 1em;
    text-decoration: none;
    transition: opacity .2s ease-in;
}
.progress__text:hover{
    opacity: .6;
}

.progress__svg{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
}
.progress__line{
    height: 17px;
    width: 2px;
    background-color: #C7C7C7;
    margin-right: 12px;
}
