.wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
    margin-top: 32px;
}

.map__item{
    width: 490px;
    height: 224px;
    overflow: hidden;
    border-radius: 8px;
}

@media (max-width: 660px) {
    .map__item{
        width: 100%;
    }
}