.form{
    position: relative;
    width: 490px;
}

.circle{
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    top: 10%;
    left: -13px;
    transform: translateY(-10%);
    background-color: #F44545;
}

.input{
    width: 0;
    height: 0;
    visibility: hidden;
    position: absolute;
    z-index: -1;
}

.cards{
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 112px 112px 112px 112px;
    grid-template-rows: 130px 130px;
    gap: 14px 14px;
    grid-template-areas:
    ". . . ."
    ". . . .";
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.card__download{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    background-color: #E9E9E9;
    cursor: pointer;
    border-radius: 1px;
}
.card__download:hover svg,
.card__download:hover circle{
    stroke: #00A0AB;
}

svg, circle{
    transition: stroke .2s linear;
}

.svg{
    margin-bottom: 11px;
}

.text{
    font-family: 'Roboto' ,sans-serif;
    font-size: 12px;
    font-weight: 400;
    color: #C7C7C7;
    text-align: center;
}

@media (max-width: 620px) {
    .form{
        width: 100%;
    }
    .cards{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .card__download{
        width: 112px;
        height: 130px;
    }
}
