.container{
    max-width: 490px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.accordionInfo{
    width: 490px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.image{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 202px;
}

.svg{
    margin-right: 10px;
}

.btn{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
}

.arrow{
    padding: 0 0 3px 1px;
    transition: transform .3s ease-in;
}

.about{
    margin: 0;
    padding: 4px 2px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #00A0AB;
    text-transform: capitalize;
}

.text__block{
    margin: 0;
}

.info{
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: 400;
    color: #5A5A5A;
}

.info__arrow{
    color: #C7C7C7;
    margin: 0;
}

@media (max-width: 620px) {
    .accordionInfo{
        flex-direction: column;
        flex-wrap: wrap;
        width: 70%;
    }
}