.section{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    width: fit-content;
}

.section + .section{
    margin-top: 40px;
}

.section__info + .section__info{
    margin-top: 23px;
}

.item{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 148px;
    margin-right: 10px;
}

.title{
    padding: 8px 0 0 2px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #5A5A5A;
}


@media (max-width: 780px) {
    .section{
        flex-wrap: wrap;
    }
}


@media (max-width: 660px) {
    .section{
        flex-wrap: wrap;
        flex-direction: column;
    }
}
