.form{
    display: flex;
    width: 490px;
    position: relative;
}

.circle{
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    top: 10%;
    left: -13px;
    transform: translateY(-10%);
    background-color: #F44545;
}

.textarea{
    box-sizing: border-box;
    width: 490px;
    height: 128px !important;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #5A5A5A;
    resize: none;
    border: 1px solid #8F8F8F;
    border-radius: 8px;
    padding: 8px 16px;
    outline: #00A0AB;
}

.textarea::placeholder{
    color: #C7C7C7;
}

.textarea:focus{
    color: #5A5A5A;
    border-color: #00A0AB !important;
}



.conditions{
    position: absolute;
    bottom: -20px;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    line-height: 1.75em;
    color: #C7C7C7;
}

@media (max-width: 610px) {
    .textarea{
        width: 70%;
    }
}

@media (max-width: 540px) {
    .textarea{
        width: 50%;
    }
}

@media (max-width: 400px) {
    .textarea{
        width: 36%;
    }
}