.form{
    position: relative;
    display: flex;
    flex-direction: column;
}

.select{
    display: flex;
    align-items: center;
    width: 264px;
    height: 32px;
    border: 1px solid #8F8F8F;
    box-sizing: border-box;
    border-radius: 8px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px !important;
    padding: 8px 16px !important;
    background-color: #ffffff !important;
    cursor: pointer;
}

.form + .form{
    margin-top: 32px;
}
.circle{
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    top: 50%;
    left: -13px;
    transform: translateY(-50%);
    background-color: #F44545;
}

.phone{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
}

.phone__item{
    display: flex;
    flex-direction: row;
}

.checkbox{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.link{
    color: #00A0AB;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 12px;
}

@media (max-width: 550px) {
    .select{
        width: 86%;
    }
    .phone__item{
        flex-direction: column;
    }
}